* {
  font-family: Helvetica, Arial, sans-serif;
}

body {
  padding: 40px;
}

input {
  color: grey;
  font-size: 1.2rem;
  width: 100%;
}

.card-grid {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.card {
  align-content: center;
  border: 1px solid gray;
  border-radius: 6px;
  overflow: hidden;
  margin-bottom: 10px;
  margin-right: 10px;
  height: 200px;
  width: 400px;
}

.card p {
  padding: 5px;
  text-align: center;
}

.card img {
  height: 100%;
  width: 100%;
}